body {
  background-color: #0000;
  background-image: url("logo.6718beec.jpg");
  background-size: 100%;
  margin: 0% 0% 2%;
  padding: 0;
  font-family: Arial, sans-serif;
  overflow-x: hidden;
}

body::-webkit-scrollbar {
  display: none;
}

.container {
  margin: 0 auto;
  position: relative;
  overflow-x: hidden;
}

header {
  z-index: 1000;
  background-color: #333;
  width: 100%;
  position: fixed;
