  box-shadow: inset 0 -50px 36px -28px #dfd5d559;
}

.modal {
  z-index: 1001;
  background-color: none;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal-title {
  text-align: center;
  width: 100%;
  margin-top: -40px;
  font-size: 22px;
}

.modal-content {
  background-color: #fefefe;
  border: 1px solid #888;
  border-radius: 5%;
  flex-direction: column;
  align-items: center;
  width: 30%;
  max-width: 300px;
  margin: 3% auto;
  padding: 15px;
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
}

.modal-anim {
  animation: .3s linear forwards modal-show;
}

@keyframes modal-show {
  0% {
    transform: translate(-150%, -150%)rotate3d(0, 0, 1, 0);
  }

  50% {
    transform: translate(-100%, -125%)rotate3d(0, 0, 1, .5turn);
  }

  100% {
    transform: translate(-50%, -100%)rotate3d(0, 0, 1, 1turn);
  }
}

.close {
  color: #a52222;
  align-self: flex-end;
  font-size: 40px;
  font-weight: 900;
}

.close:hover, .close:focus {
  color: #000;
  cursor: pointer;
  text-decoration: none;
}

.form {
  flex-direction: column;
  align-items: center;
  width: 70%;
  display: flex;
}

.form label {
  text-align: left;
  width: 100%;
  margin-bottom: 5px;
}

.form input {
  border: 1px solid #ccc;
  border-radius: 4px;
