  top: 11%;
  left: 50%;
  transform: translateX(-50%);
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.menuAvatar {
  color: #fff;
  z-index: 0;
  cursor: pointer;
  transform: translateX(-50%, -50%);
  background: none;
  border: none;
  border-radius: 50%;
  width: 13vw;
  height: 13vw;
  position: fixed;
  top: 5.2%;
  left: 17.7%;
}

.menuAvatar:before {
  content: "";
  -webkit-mask-composite: xor;
  -webkit-mask-composite: source-in;
  -webkit-mask-source-type: auto, auto;
  background: conic-gradient(#e1211a, #0000 30deg 120deg, #00f8d3 150deg 180deg, #0000 210deg 300deg, #e11e1e 330deg);
  border-radius: 50%;
  padding: 10px;
  animation: 20s linear infinite rotate;
  position: absolute;
  inset: -8px;
  -webkit-mask-image: linear-gradient(#000 0 0), linear-gradient(#000 0 0);
  -webkit-mask-position: 0 0, 0 0;
  -webkit-mask-size: auto, auto;
  -webkit-mask-repeat: repeat, repeat;
  -webkit-mask-clip: content-box, border-box;
  -webkit-mask-origin: content-box, border-box;
  mask-composite: intersect;
  mask-mode: match-source, match-source;
}

.menuAvatar:after {
  content: "";
  filter: blur(60px);
  opacity: .5;
  background: radial-gradient(100px at 150px 50px, #e91408 100%, #0000), radial-gradient(100px at right 150px bottom 50px, #15c5d5 100%, #0000);
  animation: 20s linear infinite rotate;
  position: absolute;
  inset: -50px;
}

.menuAvatar > img {
  object-fit: cover;
  opacity: .9;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  position: relative;
}

